import React, { FunctionComponent } from "react";
import './Button.css';

export interface ButtonProps {
  isLight?: boolean;
  isWide?: boolean;
  title?: string;
  onClick?: () => void;
}

export const Button: FunctionComponent<ButtonProps> = ({ isLight, isWide, title, onClick, children }) => {
  var classes = ["button"];
  if (isLight) classes.push("light");
  if (isWide) classes.push("wide");
  return <div className={classes.join(" ")} title={title} onClick={onClick}>{children}</div>;
}
