import React from "react";
import './LoadStatus.css'
import logo from '../../Assets/boussole.svg'

export type LoadStatusProps = {
  done: number;
  total: number;
  message: string;
};

export const LoadStatus: React.FC<LoadStatusProps> = ({ done, total, message }) => {
  const rects: JSX.Element[] = []

  for (var i = 0; i < total; i++) {
    rects.push(<rect
      x={i * 120}
      y="2.5"
      width="95"
      height="95"
      rx="25"
      ry="25"
      style={{
        fill: i < done ? "rgb(125, 160, 191)" : "rgb(255, 255, 255)",
        strokeWidth: 5,
        stroke: "rgb(67, 106, 140)"
      }
      }
    />
    )
  }

  return <div className="load-status">
    <img src={logo} />
    <svg className="load-bar" viewBox={`0 0 ${total * 120 - 20} 100`}>
      {rects}
    </svg>
    {message}
  </div >;
}
