import { AppState } from "./appState";
import { AppActionBase, AppAction, useAppDispatch } from './store';
import { newGuid } from "../Helpers/guidHelper";
import { useSelector } from "react-redux";

// *** Model ***

export type TaskState = "created" | "in-backlog" | "active" | "finished" | "abandoned";

export interface NewTask {
  state: TaskState;
  title: string;
  description: string; // may be empty, never null or undefined
}

export interface Task extends NewTask {
  id: string;
  created: Date;
  modified: Date;
  dateClosed?: Date;   // date the task was closed. undefined if "state" is not "closed".
}

export interface TaskModel {
  [id: string]: Task;
}

export const getNewTask = (): NewTask => ({
  state: "created",
  title: "",
  description: "",
});

// *** Actions ***

interface UpdateTaskAction extends AppActionBase {
  type: "UpdateTask";
  id: string;
  task: Partial<NewTask>;
}

interface CreateTaskAction extends AppActionBase {
  type: "CreateTask";
  task: NewTask;
}

export type TaskActions = UpdateTaskAction | CreateTaskAction;

// *** Reducer ***

function updateTask(tasks: TaskModel, id: string, updates: Partial<NewTask>) {
  var task = tasks[id];

  if (!task) throw new Error(`No task found with id {id}`);

  tasks[id] = {
    ...task,
    ...updates,
  };

  return { ...tasks };
}

function createTask(tasks: TaskModel, task: NewTask) {
  const id = newGuid();
  const now = new Date();

  tasks[id] = {
    ...task,
    id,
    created: now,
    modified: now,
  };

  return { ...tasks };
}

export function taskReducer(tasks: TaskModel = {}, action: AppAction): TaskModel {
  switch (action.type) {
    case "UpdateTask": return updateTask(tasks, action.id, action.task);
    case "CreateTask": return createTask(tasks, action.task);
    default: return tasks;
  }
}

// *** Service ***

export const useTasks = () => useSelector<AppState, TaskModel>(appState => appState.tasks);

export interface TaskService {
  createTask: (task: NewTask) => void;
  updateTask: (id: string, task: Partial<NewTask>) => void;
}

export function useTaskService(): TaskService {
  const dispatch = useAppDispatch();

  return {
    createTask: task => dispatch({ type: "CreateTask", task }),
    updateTask: (id, task) => dispatch({ type: "UpdateTask", id, task }),
  };
}

export const newState = (state: TaskState): Partial<Task> => {
  return {
    state,
    dateClosed: state === "finished" || state === "abandoned" ? new Date() : undefined,
  }
}