import React from "react";

type TaskFilterProps = {
  visible: boolean;
  text: string;
  textChanged: (text: string) => void;
}

export const TaskFilter: React.FunctionComponent<TaskFilterProps> = ({ visible, text, textChanged }) => {
  return <div className={`tasksFilter${visible ? " visible" : ""}`}>
    <div className="tasksFilterInner">
      <input className="taskFilterText" value={text} onChange={ev => textChanged(ev.target.value)} />
    </div>
  </div>
}
