import React from "react";
import "./Tasks.css";
import { TaskComponent } from "./TaskComponent";
import { TaskForm } from "./TaskForm";
import { useTasks, TaskModel, Task, TaskState } from "../../Store/tasks";
import { Button } from "../Utility/Button/Button";
import { PageLayout } from "../App/PageLayout";
import { FilterImage } from "../../Assets/FilterImage";
import { TaskFilter } from "./TaskFilter";

type TaskFormState = {
  isOpen: boolean;
  taskId?: string;
};

function getStateOrder(state: TaskState): number {
  switch (state) {
    case "active": return 0;
    case "in-backlog": return 1;
    case "created": return 2;
    case "finished": return 3;
    case "abandoned": return 4;
  }
}

function compare(task1: Task, task2: Task): number {
  if (task1.state !== task2.state) {
    return getStateOrder(task1.state) < getStateOrder(task2.state) ? -1 : 1;
  }

  // both tasks have the same state

  return task1.title.localeCompare(task2.title);
}

function sortAndFilterTasks(tasks: TaskModel, text: string): Task[] {
  var result = Object.keys(tasks)
    .map(id => tasks[id])
    .sort(compare);

  if (text) {
    text = text.toLowerCase();
    result = result.filter(task =>
      task.title.toLowerCase().includes(text) ||
      task.description.toLowerCase().includes(text))
  }

  return result;
}

export function TaskList() {
  var tasks = useTasks();
  const [taskForm, setTaskForm] = React.useState<TaskFormState>({ isOpen: false });
  const [filterVisible, setFilterVisible] = React.useState<boolean>(false);
  const [filterText, setFilterText] = React.useState<string>("");

  /*React.useEffect(() => {
    (async () => {
      try {
        setData(await getData());
      } catch (error) {
        setData(error.toString());
      }
    })();
  }, []);*/

  return <PageLayout
    headerControls={
      <Button
        onClick={() => setFilterVisible(!filterVisible)}
        isLight={true}
        title="Recharger les donnes"
      >
        <FilterImage height={20} width={20} stroke="white" />
      </Button>
    }
  >
    <div className="taskList">
      <TaskFilter visible={filterVisible} text={filterText} textChanged={setFilterText} />
      <div className="tasks">
        {sortAndFilterTasks(tasks, filterVisible ? filterText : "").map(task => <TaskComponent key={task.id} task={task} onClick={() => setTaskForm({ isOpen: true, taskId: task.id })} />)}
      </div>
      <div className="tasksButtons">
        <Button isWide onClick={() => setTaskForm({ isOpen: true, taskId: undefined })}>+</Button>
      </div>
      {
        taskForm.isOpen &&
        <TaskForm
          task={taskForm.taskId ? tasks[taskForm.taskId] : undefined}
          onClose={() => setTaskForm({ isOpen: false })}
        />}
    </div>
  </PageLayout>;
}
