import React from "react";
import { Task, useTaskService, newState } from "../../Store/tasks";
import { TaskStateDisplay } from "./TaskStateDisplay";

export type TaskComponentProps = {
  task: Task;
  onClick: () => void;
};

export const TaskComponent: React.FunctionComponent<TaskComponentProps> = ({ task, onClick }) => {
  const { updateTask } = useTaskService();

  return <div onClick={onClick} className="task">
    <div className="title">{task.title}</div>
    <TaskStateDisplay small state={task.state} stateChanged={state => state && updateTask(task.id, newState(state))} />
  </div>;
}