import React from "react";
import { TaskState } from "../../Store/tasks";
import { Dialog } from "../Utility/Dialog/Dialog";

export type TaskStateDisplayState = {
  small?: boolean;
  state: TaskState;
  readonly?: boolean;
  stateChanged: (value: TaskState | undefined) => void;
};

function getLabel(state: TaskState): string {
  switch (state) {
    case "created": return "En attente";
    case "in-backlog": return "Dans le radar";
    case "active": return "En cours";
    case "finished": return "Termine";
    case "abandoned": return "Abandonne";
  }
}

export const TaskStateDisplay: React.FunctionComponent<TaskStateDisplayState> = ({ small, state, readonly, stateChanged }) => {
  const [isDialogOpen, setIsDialogOpen] = React.useState(false);

  const stateClicked = () => {
    if (readonly) {
      setIsDialogOpen(false);
      stateChanged(state);
    } else {
      setIsDialogOpen(true);
    }
  }

  const newStateClicked = (newState: TaskState) => {
    setIsDialogOpen(false);
    stateChanged(newState);
  }

  const renderButton = (buttonState: TaskState) => {
    if (state === buttonState) {
      return null;
    }
    return <TaskStateDisplay key={buttonState} state={buttonState} readonly={true} stateChanged={() => newStateClicked(buttonState)} />;
  }

  return <div>
    <div className={`task-state ${state}${small ? " small" : ""}`} onClick={ev => { ev.stopPropagation(); stateClicked(); }}>{!small && getLabel(state)}</div>
    {
      isDialogOpen &&
      <Dialog buttons={[{ text: "Annuler", onclick: () => { setIsDialogOpen(false); stateChanged(undefined); } }]}>
        {renderButton("created")}
        {renderButton("in-backlog")}
        {renderButton("active")}
        <div className="task-state-closed">
          {renderButton("finished")}
          {renderButton("abandoned")}
        </div>
      </Dialog>}
  </div >;
}
