import React, { useState, useMemo } from "react";
import "./Tasks.css";
import { useTaskService, Task, NewTask, getNewTask, newState } from "../../Store/tasks";
import { Dialog } from "../Utility/Dialog/Dialog";
import { Field } from "../Utility/Field/Field";
import { TaskStateDisplay } from "./TaskStateDisplay";

export type TaskFormProps = {
  task: Task | undefined;
  onClose: () => void;
};

export function TaskForm(props: TaskFormProps) {
  const id = useMemo(() => props.task ? props.task.id : undefined, [props.task]);
  const [editedTask, setEditedTask] = useState<NewTask>(props.task || getNewTask());
  const { createTask, updateTask } = useTaskService();

  const onSave = () => {
    if (id) {
      updateTask(id, editedTask);
    } else {
      createTask(editedTask);
    }
    props.onClose();
  }

  return <Dialog title={id ? "Modifier une tche" : "Nouvelle tche"} buttons={[
    { text: "Valider", onclick: onSave },
    { text: "Annuler", onclick: props.onClose }
  ]}>
    <>
      <TaskStateDisplay state={editedTask.state} stateChanged={state => state && setEditedTask({ ...editedTask, ...newState(state) })} />
      <Field label="titre" >
        <input
          type="text"
          value={editedTask.title}
          onChange={event => setEditedTask({ ...editedTask, title: event.target.value })} />
      </Field>
      <Field label="description">
        <textarea
          value={editedTask.description}
          onChange={event => setEditedTask({ ...editedTask, description: event.target.value })} />
      </Field>
    </>
  </Dialog>;
}
