import { Reducer } from "react"
import { AppState } from "./appState"
import { AppAction, AppActionBase, useThunkDispatch } from "./store"
import { useSelector } from "react-redux";
import { ThunkDispatch } from "redux-thunk";
import { dataStore } from "../DataStore/DataStoreService";
import { StoreModel, StoreTask } from "../DataStore/dataStoreModel";
import { Task, TaskModel } from "./tasks";

// *** Model ***

export interface DataStorageModel {
  status: "fetching" | "saving" | "inactive";
  dataAvailable: boolean;
  fetchError?: any;
  saveError?: any;
}

// *** Actions ***

interface ModelFetchingAction extends AppActionBase {
  type: "ModelFetching";
}

interface ModelFetchedAction extends AppActionBase {
  type: "ModelFetched";
  appState: Omit<AppState, "dataStorage" | "authState">;
}

interface ModelFetchErrorAction extends AppActionBase {
  type: "ModelFetchError";
  error: any;
}

interface ModelSavingAction extends AppActionBase {
  type: "ModelSaving";
}

interface ModelSavedAction extends AppActionBase {
  type: "ModelSaved";
}

interface ModelSaveErrorAction extends AppActionBase {
  type: "ModelSaveError";
  error: any;
}

export type DataStorageActions =
  ModelFetchingAction |
  ModelFetchedAction |
  ModelFetchErrorAction |
  ModelSavingAction |
  ModelSavedAction |
  ModelSaveErrorAction;

// *** Reducers ***

export const dataStorageReducer: Reducer<AppState, AppAction> = (appState, action): AppState => {
  if (appState === undefined) throw new Error("dataStorageReducer: Unexpected undefined appState.");

  switch (action.type) {
    case "ModelFetching": return { ...appState, dataStorage: { ...appState.dataStorage, status: "fetching", fetchError: undefined } };
    case "ModelFetched": return { ...appState, ...action.appState, dataStorage: { ...appState.dataStorage, status: "inactive", dataAvailable: true, fetchError: undefined } };
    case "ModelFetchError": return { ...appState, dataStorage: { ...appState.dataStorage, status: "inactive", dataAvailable: false, fetchError: action.error } };
    case "ModelSaving": return { ...appState, dataStorage: { ...appState.dataStorage, status: "saving", saveError: undefined } };
    case "ModelSaved": return { ...appState, dataStorage: { ...appState.dataStorage, status: "inactive", saveError: undefined } };
    case "ModelSaveError": return { ...appState, dataStorage: { ...appState.dataStorage, status: "inactive", saveError: action.error } };
    default: return appState;
  }
}

// *** Conversion from Redux Store to Data Storage and vice versa ***

export const useDataStorage = () => useSelector<AppState, DataStorageModel>(appState => appState.dataStorage);

function getStoreTask(task: Task): StoreTask {
  return {
    state: task.state === "created" ? undefined : task.state,
    id: task.id,
    title: task.title,
    description: task.description || undefined,
    created: task.created,
    modified: task.modified,
    dateClosed: task.dateClosed,
  }
}

function getStoreModel(appState: AppState): StoreModel {
  const tasks: StoreTask[] = [];
  for (const taskId in appState.tasks) {
    tasks.push(getStoreTask(appState.tasks[taskId]));
  }

  return { tasks };
}

function getTask(storeTask: StoreTask): Task {
  return {
    state: storeTask.state || "created",
    id: storeTask.id,
    title: storeTask.title,
    description: storeTask.description || "",
    created: storeTask.created,
    modified: storeTask.modified,
    dateClosed: storeTask.dateClosed,
  }
}

function getAppState(storeModel: StoreModel): Omit<AppState, "dataStorage" | "authState"> {
  const tasks: TaskModel = {}

  storeModel.tasks.forEach(storeTask => tasks[storeTask.id] = getTask(storeTask));

  return { tasks };
}

// *** Service ***

export const fetchData = (thunkDispatch: ThunkDispatch<AppState, undefined, AppAction>) => {
  thunkDispatch(async (dispatch, getState) => {
    const state = getState();
    if (state.dataStorage.status !== "inactive") {
      throw Error("An action is already ongoing.");
    }

    dispatch({ type: "ModelFetching" });

    try {
      const storeModel = await dataStore.getData();
      dispatch({ type: "ModelFetched", appState: getAppState(storeModel) });
    }
    catch (error) {
      dispatch({ type: "ModelFetchError", error });
    }
  });
}

export function useFetchData() {
  const thunkDispatch = useThunkDispatch();
  return () => fetchData(thunkDispatch);
}

export function useSaveData() {
  const thunkDispatch = useThunkDispatch();

  return () => thunkDispatch(async (dispatch, getState) => {
    const appState = getState();
    if (appState.dataStorage.status !== "inactive") {
      throw Error("An action is already ongoing.");
    }

    dispatch({ type: "ModelSaving" });

    try {
      await dataStore.saveData(getStoreModel(appState));
      dispatch({ type: "ModelSaved" });
    }
    catch (error) {
      dispatch({ type: "ModelSaveError", error });
    }
  });
}
